import {Injectable} from '@angular/core';
import * as io from 'socket.io-client';
import { Observable} from 'rxjs';
import * as Rx from 'rxjs'
import {environment} from '../environments/environment';
import { R3ExpressionFactoryMetadata } from '@angular/compiler/src/render3/r3_factory';

@Injectable()
export class WebsocketService {

  private socket; // Socket that connects to my socket.io server
  private _ws_url;

  constructor() {}

  connect():Rx.Subject<MessageEvent>{
    this.socket = io.connect('localhost:4200');

    let observable = new Observable(observer => {
      this.socket.on('message', (data)=> {
        // console.log('received a message from websocket server');
        observer.next(data);
      })
      return ()=> {
        this.socket.disconnect();
      }
    })

    let observer = {
      next: (data:Object) => {
        this.socket.emit('message', data);
      },
    };

    return Rx.Subject.create(observer,observable);

  }

}
