import { Injectable } from '@angular/core';
import { HttpHeaders, HttpClient } from '@angular/common/http';
import { UserModel } from '../../models/user.model';



@Injectable({
  providedIn: 'root'
})
export class AuthService {

  // Variables
  private apiUrl: String = 'http://localhost:7866/api';

  constructor(private http : HttpClient ) { }

  public registere (data: UserModel ): Promise<any>{
    //Configurer le header de la requête
    let myHeader = new HttpHeaders();
    myHeader.append('Content-Type', 'application/json');

    // Créer la requête
    return this.http.post( `${this.apiUrl}/user` , data , {headers:myHeader})
    .toPromise()
    .then(this.getData)
    .catch(this.handleError)
  }

  public login (data: UserModel ): Promise<any>{
    //Configurer le header de la requête
    let myHeader = new HttpHeaders();
    myHeader.append('Content-Type', 'application/json');

    // Créer la requête
    return this.http.post( `${this.apiUrl}/login` , data , {headers:myHeader})
    .toPromise()
    .then(this.getData)
    .catch(this.handleError)
  }


  // Get the API Response
  private getData(res:any){
    return res || {};
  }

  // Get the API error
  private handleError (err:any){
    return Promise.reject(err.error)
  }
}
