// Imports
import { Component,OnInit, ViewChild, ElementRef } from '@angular/core';
import { ChatService } from "./chat.service";
import { UserModel } from './models/user.model';
import { AuthService } from './services/auth/auth.service';
// import { Socket } from './pages/chat/interfaces';
// import * as socketIo from 'socket.io-client';

import { Observable} from 'rxjs';
import * as Rx from 'rxjs'



// Definitions
@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  providers:[AuthService],
  styleUrls: ['./app.component.css']


})

// Exports
export class AppComponent {

  @ViewChild('scrollMe') private myScrollContainer: ElementRef;
  public myTitle: String;
  public formData: UserModel;
  public loginData: UserModel;
  private socket;


  //SET VARIABLES
  test1:string;
  user1: string;
  public messages=[];
  public msg=[];

  constructor(
    private authService : AuthService,
    private chat: ChatService
  ){
    this.myTitle = 'Chat Angular';
    this.formData = {
      email : undefined,
      name: undefined,
      password:undefined
    };

    this.loginData={
      email:undefined,
      password: undefined
    }
  }

  connect():Rx.Subject<MessageEvent>{
    this.socket = io.connect('localhost:4200');

    let observable = new Observable(observer => {
      this.socket.on('message', (data)=> {
        observer.next(data);
      })
      return ()=> {
        this.socket.disconnect();
      }
    })

    let observer = {
      next: (data:Object) => {
        this.socket.emit('message', JSON.stringify(data));
      },
    };

    return Rx.Subject.create(observer,observable);

  }


  ngOnInit(){
    this.chat.messages.subscribe(msg=>{
      this.messages.push(msg.text);
    })

    this.scrollToBottom();
  }

  sendMessage(user,message){

    if(message!=''&&user!=''){

      this.chat.sendMsg(user,message);
      this.test1='';
    }
  }

   /** ENTER SEND MESSAGE **/
   enterPressed($event){
    if($event.which === 13) //ENTER pressed
    {
      this.sendMessage(this.user1,this.test1);
    }
  }

  scrollToBottom(): void {
    try {
      this.myScrollContainer.nativeElement.scrollTop = this.myScrollContainer.nativeElement.scrollHeight;
    } catch(err) { }
  }

  ngAfterViewChecked() {
    this.scrollToBottom();
  }


  public formSubmit(){
    console.log(this.formData);
    this.authService.registere(this.formData)
    .then( apiResponse => console.log(apiResponse))
    .catch(apiResponse => console.error(apiResponse));
  }


  public loginUser(){
    this.authService.login(this.loginData)
    .then( apiResponse => console.log(apiResponse))
    .catch(apiResponse => console.error(apiResponse));
  }
}
